.header {
  font-family: DM Sans, sans-serif;
}

.header__menu {
  z-index: 999;
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  margin: 0;
  padding: 18px 20px;
  display: flex;
  position: relative;
}

.header__aside {
  justify-content: center;
  gap: 14px;
