}

.header__button-open, .header__button-close {
  width: 28px;
  height: 28px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  padding: 0;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
  top: 4px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  transition: all .4s;
  position: absolute;
  inset: 0;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background: none, #f7f7f7;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.logo {
  align-items: center;
